.slider {
    position: relative;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #010606;
  }

  @media screen and (max-width: 480px) {
    .slider{
      height: 80vh;
    }
    }
  
  .image {
    width: 1000px;
    height: 600px;
    border-radius: 10px;
  }

  @media screen and (max-width: 480px) {
    .image {
      width: 275px;
      height: 165px;
      border-radius: 10px;
    }
    }
  
  .right-arrow {
    position: absolute;
    top: 50%;
    right: 32px;
    font-size: 3rem;
    color: #39FF14;
    z-index: 10;
    cursor: pointer;
    user-select: none;
  }

  @media screen and (max-width: 480px) {
    .right-arrow{
      top: 48%;
      font-size: 2rem;
    }
    }
  
  .left-arrow {
    position: absolute;
    top: 50%;
    left: 32px;
    font-size: 3rem;
    color: #39FF14;
    z-index: 10;
    cursor: pointer;
    user-select: none;
  }
  
  @media screen and (max-width: 480px) {
    .left-arrow{
      top: 48%;
      font-size: 2rem;
    }
  }

  .slide {
    opacity: 0;
    transition-duration: 1s ease;
  }
  
  .slide.active {
    opacity: 1;
    transition-duration: 1s;
    transform: scale(1.08);
  }
  